export default {
	data() {
		return {
			bulletinList: [],
			num: 0,
		};
	},
	onLoad() {
		let mid = this.getMerchantInfo().id;
		if(mid){
			this.getSelfBulletin();
		}else{
			uni.showToast({
				title:"您还未认证商家",
				icon:"none"
			})
			setTimeout(()=>{
				uni.switchTab({
					url:"../tabbar/user"
				})
			},1000)
		}
	},
	methods: {
		getSelfBulletin() {
			uni.request({
				url: this.prefix + "/getNewsList",
				data: {
					merchantId: this.getMerchantInfo().id
				},
				success: (res) => {
					if (res.data.code === 200) {
						this.bulletinList = res.data.data
						console.log(this.bulletinList)
					} else {
						uni.showToast({
							title: "获取资讯异常",
							icon: "none"
						})
					}
				}
			})
		},
		toNews(id){
			uni.navigateTo({
				url:"./newsDetail?id="+id
			})
		}
	}
}
